th:75%;height:30px;cursor:pointer;margin:0 10px 10px}.btn{display:flex;justify-content:space-around;align-items:center;font-family:"Roboto",sans-serif;font-size:1.1rem;font-weight:400;border-radius:5px;border:1px solid #bbb;background:#00420f;color:#eee}.btn .fa{color:#eee}#scroll-top{position:fixed;right:20px;bottom:20px;border-style:none;color:#ffe657;background:transparent}#scroll-top:hover{cursor:pointer}#scroll-top::after{content:"scroll to top";align-self:center;display:block;margin:10px;font-family:"Roboto",sans-serif;font-weight:500}@media screen and (max-width: 400px){#navbar{font-size:1.1rem;max-width:100vw;flex-wrap:wrap}.company{width:calc(100vw - 45%);margin:20px 0;font-size:1.7rem}.company img{width:80px}.company p::after{font-size:1rem;margin:2px 0px 0 23px}#section-one-content{display:grid;grid-template-columns:40px 200px;grid-template-rows:450px;margin:60px 0}#banner{width:200px;margin:10px 0 0 -30px}#banner img{width:100%}#main-deals{-webkit-transform:translateX(-13px);transform:translateX(-13px);margin:0}#headline{font-size:2rem;margin-right:-120px}.game-card{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:0 5px}.game-card div{width:85px;height:120px}.game-card p{font-size:0.8rem;line-height:1.2}.btn{font-size:0.85rem}.section-two{flex-wrap:wrap}#sidebar{width:100%;text-align:center;padding-bottom:20px}#sidebar header{font-size:1rem}#sidebar a{font-size:0.8rem;display:inline-block;min-width:110px;margin:5px 0 -5px}#section-two-deals{width:100%;margin:10px;padding:10px}#section-two-deals .game-card{width:120px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:10px -7px}#scroll-top{display:none}}@media (min-width: 400px) and (max-width: 500px){#navbar{font-size:1.1rem;max-width:100vw;flex-wrap:wrap}.company{width:calc(100vw - 45%);margin:20px 0;font-size:1.7rem}.company img{width:80px}.company p::after{font-size:1rem;margin:2px 0px 0 23px}#section-one-content{display:grid;grid-template-columns: